<!--选择货物
 * @Author: your name
 * @Date: 2021-01-07 23:32:06
 * @LastEditTime: 2021-01-07 23:32:07
 * @LastEditors: Please set LastEditors
 * @Description: In User Settings Edit
 * @FilePath: \things_wechat\pages\things\user\demand\components\selectFreight\selectFreight.vue
-->
<template>
   <view class="cu-modal" :class="modalShow?'show':''" @tap="hideModal">
        <view class="cu-dialog">
            <swiper class="swiper" :indicator-dots="indicatorDots" :current="current" :autoplay="autoplay" :interval="interval" :duration="duration" @change="changeCurrentFun">
                <swiper-item v-for="(item,index) in freightList" :key="index">
                    <view class="swiper-item">
                       <!-- 货物信息 -->
                        <view class="freight_info">
                            <view class="things_width_full flex justify-start align-center user_info">
                                <view class="imageBox">
                                    <image class="user_cover" :src="item.grabsPath" mode="widthFix"/>
                                </view>
                                <view class="name">{{item.grabsRealname}}</view>
                                <view class="flex justify-start align-center score">
                                    <image class="score_icon" src="/static/images/global/score_icon.png" mode="widthFix" />
                                    <view class="score_num">{{item.grabsReputation}}</view>
                                    <view class="identity_text">{{item.grabsUserType}}</view>
                                </view>
                            </view>
                            <view class="freight_single_info">装车地点：{{item.grabsLoadingAddress}}</view>
                            <view class="freight_single_info">卸车地点：{{item.grabsUnloadingAddress}}</view>
                            <view class="freight_single_info">货物名称：{{item.grabsGoodsName}}</view>
                            <view class="freight_single_info">损耗范围：{{item.grabsLossRange}}</view>
                            <view class="freight_single_info">备注：{{item.grabsRemark}}</view>
                        </view>
                    </view>
                </swiper-item>
            </swiper>
            <button class="cu-btn lg bg-green select_btn" @tap.stop="selectFreightFun">选择</button>
        </view>
    </view>
</template>
<script>
export default {
    props: {
        modalShow: {
            type: Boolean,
            default: false,
        },
        freightList: {
            type: Array,
            default: []
        }
    },
    data() {
        return {
            indicatorDots: true,
            autoplay: false,
            interval: 2000,
            duration: 500,
            current: 0,
        }
    },
    methods: {
        /**改变current
         * @description: 
         * @param {*} e
         * @return {*}
         */
        changeCurrentFun(e) {
            this.current = e.detail.current;
        },
        /**隐藏弹框
         * @description: 
         * @param {*}
         * @return {*}
         */
        hideModal() {
            this.modalShow = false;
            this.$emit("closeSelectFreightModal",false);
        },
        /**选择货物
         * @description: 
         * @param {*} e
         * @return {*}
         */
        selectFreightFun(e) {
            // this.modalShow = false;
            this.$emit("selectFreightFun",this.freightList[this.current]);
        },
    }
}
</script>
<style lang="scss" scoped>
    .cu-modal {
        z-index: 3;
    }
    .swiper {
        height: 600upx;
    }
    .freight_info {
        padding: 19upx 32upx 0upx;
        .user_info {
            margin-bottom: 35upx;
            .user_cover {
                width: 80upx;
                height: 80upx;
                border-radius: 50%;
            }
            .name {
                font-size: 32upx;
                font-weight: normal;
                color: #111111;
                margin-left: 32upx;
            }
            .score {
                margin-left: 35upx;
                .score_icon {
                    width: 30upx;
                    height: auto;
                }
                .score_num {
                    font-size: 32rpx;
                    font-weight: normal;
                    font-style: italic;
                    color: #ff802c;
                    margin-left: 8upx;
                    line-height: 1;
                } 
                .identity_text {
                    font-size: 28upx;
                    font-weight: normal;
                    color: #ff802c;
                    margin-left: 12upx;
                    line-height: 1;
                }
            }
        }
        .freight_double_info {
            margin-bottom: 33upx;
            .freight_left_info,.freight_right_info {
                font-size: 28upx;
                font-weight: normal;
                color: #111111;
                line-height: 28upx;
            }
            .freight_left_info {
                width: 340upx;
            }
            .freight_right_info {
                flex: 1;
            }
        }
        .freight_single_info {
            font-size: 28rpx;
            font-weight: normal;
            color: #111111;
            margin-bottom: 33upx;
            text-align: left;
        }
        .order_type {
            margin-bottom: 0!important;
            .type_text {
                color: #ff1e23;
            }
        }
        >view:last-child {
            margin-bottom: 30upx;
        }
    }
    .select_btn {
        width:100%;
        border-radius: 0;
    }
</style>